@mixin mobile{
    @media screen and (max-width: 480px) {
        @content;
    }
}

@mixin tablet{
    @media screen and (max-width: 768px) {
        @content;
    }
}

@mixin laptop{
    @media screen and (max-width: 1200px) {
        @content;
    }
}

* {
    margin: 0;
}

.formContainer {
    background-color: #85E4F3;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.formWrapper{
    background-color: #fff;
    padding: 20px 60px;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;

    .logo{
        color: #5ab1be;
        font-weight: bold;
        font-size: 30px;
    }

    .title{
        color: #5ab1be;
        font-size: 20px;
        margin-bottom: 10px;
    }

    form{
        display: flex;
        flex-direction: column;
        gap: 15px;

        input{
            padding: 15px;
            border: none;
            width: 250px;
            border-bottom: 1px solid #5ab1be;

            &::placeholder{
                color: rgb(175, 175, 175);
            }

        }
        .img {
            width: 40px;
            height: 40px;
        }    

        button{
            background-color: #5ab1be;
            color: rgba(255, 255, 255, 0.909);
            border: none;
            border-radius: 5px;
            margin-top: 0px;
            font-weight: bold;
            padding: 10px;
            cursor: pointer;
        }

        label{
            display: flex;
            align-items: center;
            gap: 10px;
            color: #5ab1be;
            font-size: small;
            cursor: pointer;
        }
    }
        
        p{
            color: #5ab1be;
            font-size: 15px;
            margin-top: 10px;
            cursor: pointer;
        }   
}

.home {
    background-color: #cef2f7;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.container{
    border: 1px solid #fff;
    border-radius: 10px;
    width: 65%;
    height: 80%;
    display: flex;
    overflow: hidden;
    @include tablet{
        width: 90%;
    }

    .sidebar{
        flex: 1; 
        background-color: #fff;
        position: relative;

    
        .navbar{
            display: flex;
            align-items: center;
            background-color: #428a95;
            height: 50px;
            padding: 10px;
            justify-content: space-between;
            color: #fff;

            .logo{
                font-weight: bold;
                @include tablet{
                    display: none;
                }
            }

            .user{
                display: flex;
                gap: 10px;
                align-items: center;
            }

            img{
                background-color: #fff;
                height: 24px;
                width: 24px;
                border-radius: 50%;
                object-fit: cover;
            }

            button{
                background-color: #39737c;
                color: #fff;
                border: none;
                border-radius: 5px;
                padding: 5px 10px;
                font-size: 10px;
                cursor: pointer;
                @include tablet{
                    position: absolute;
                    bottom: 10px;
                }
               
            }
        }

        .search{
            border-bottom: 1px solid lightgray;

            input{
                background-color: transparent;
                border: none;
                outline: none;
                color: #a1a1a1;
                padding: 12px;;
            }
        }

        .userChat{
            padding: 10px;
            display: flex;
            align-items: center;
            gap: 10px;
            color: rgb(0, 0, 0);
            cursor: pointer;

            &:hover{
                background-color: #c1c1c1;
            }
            
            img{
            width: 40px;
            height: 40px;
            border-radius: 50%;
            object-fit: cover;

        }
        .userChatInfo{
            .searchUserName{
                color: black;
            }
            span{
                font-size: 16px;
                font-weight: 500;
            }
            p{
                color: rgb(132, 132, 132);
                font-size: 14px;
            }
        }
            
        }
    }
    .chat{flex: 2;

        .chatInfo{
            height: 50px;
            background-color: #77d1df;
            display: flex;
            justify-content: space-between;
            color: white;
            align-items: center;
            padding: 10px 20px;

            .icon{
                font-size: 25px;
                margin-right: 10px;
                cursor: pointer;
            }
        }

        .messages{
            background-color: #e9e9e9;
            padding: 10px;
            height: calc(100% - 160px);
            overflow: scroll;
            overflow-x: hidden;

            .message{
                display: flex;
                gap: 20px;
                margin-bottom: 20px;
               

                .messageInfo{
                    display: flex;
                    flex-direction: column;
                    color: gray;
                    font-size: 13px;
                    font-weight: 300;
                    img{
                        width: 40px;
                        height: 40px;
                        border-radius: 50%;
                        object-fit: cover;
                    }
                }
                .messageContent{
                    max-width: 80%;
                    display: flex;
                    flex-direction: column;
                    gap: 10px;

                    p{
                        color: #fff;
                        background-color: #77d1df;
                        padding: 10px 20px;
                        border-radius: 0px 10px 10px 10px;
                        max-width: max-content;
                    }
                    img{
                        width: 50%;
                    }
                }

                &.owner{
                    flex-direction: row-reverse;

                    .messageContent{
                        align-items: flex-end;

                    p{
                        background-color: #428a95;
                        color: white;
                        border-radius: 10px 0px 10px 10px;
                    }

                }
                }

                
            }
        }

        

        .input{
            background-color: white;
            height: 50px;
            padding: 10px;
            display: flex;
            align-items: center;
            justify-content: space-between;

            input{
                width: 100%;
                border: none;
                outline: none;
                color: #296456;
                font-size: 18px;

                &::placeholder{
                    color: rgb(165, 165, 165);
                }
            }

            .send{
                display: flex;
                align-items: center;
                gap: 10px;
                justify-content: center;
                cursor: pointer;
            }
            img {
                height: 24px;
                cursor: pointer;
              }
            .imgAttach{
                margin-top: 10px;
            }  
            .sendButton{
                color: #5ab1be;
            }
        }
    
    }
}
